<template>
    <div class="container">
        <div class="row">
            <div v-for="item in list" :key="item.id" class="col-md-6 offset-md-3">
                <h4>{{ item.title }}</h4>

                <p style="text-indent:2em;" class="c1">
                    {{ item.content }}
                    <span
                        style="color: rgba(0, 0, 255, 0.671);"
                        @click="watchAll(item.id, item.title)"
                        align="right"
                    >...阅读更多</span>
                </p>

                <div class="divide"></div>
            </div>
        </div>
        <VueEternalLoading :load="load"></VueEternalLoading>
    </div>
</template>




<script>
const PAGE_SIZE = 10;
import { VueEternalLoading } from '@ts-pro/vue-eternal-loading';
import http from '../axios';
import { bid } from '../constant';
export default {
    name: "colorlist",
    components: { VueEternalLoading },

    data() {
        return {
            list: []
        }
    },
    created() {
        // this.load();
    },
    methods: {
        // 5fe35a393848694a84bd5006

        // 616f7073db6d00004d001ab2
        async load(action) {
            var res = await http.get("/color/" + bid)
            this.list = this.list.concat(res.data)
            action.loaded(res.data.length, PAGE_SIZE);
        },
        watchAll(cid, name) {
            this.$router.push({
                name: "readcolor",
                params: {
                    cid: cid, name: name
                }
            })
        },


    },

}
</script>

<style>
.c1 {
    padding-top: 10px;
}

.container {
    padding-top: 20px;
}

.divide {
    width: 100%;
    height: 1px;
    border-top: 1px solid #ccc;
    padding-bottom: 20px;
}
</style>